export function graphReducer(state, action){
    if (action.type === "GRAPH_CHANGE_RANGE"){
        return {
            ...state,
            dayRange: action.data
        }
    }else if (action.type === "GRAPH_ADD_ONE"){
        return {
            ...state,
            graphDisplay: [
                ...state.graphDisplay,
                action.data
            ]
        }
    }else if (action.type === "GRAPH_REMOVE_ONE"){
        return {
            ...state,
            graphDisplay: state.graphDisplay.filter((elt,  idx) => idx !== action.data)
        }
    }else{
        console.log(action);
        console.warn("Action not recognised");
        return state;
    }
}