import React from "react";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { makeStyles } from "@material-ui/core/styles";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Button from "@material-ui/core/Button";
import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from "@material-ui/icons/Delete";
import HelpOutlineIcon from "@material-ui/icons/HelpOutline";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import { cloneDeep } from "lodash";
import Tooltip from "@material-ui/core/Tooltip";
import tooltips from "./tooltips";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "95%",
    margin: "auto",
    padding: "1rem",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
}));

function keyToFieldName(key) {
  return key.replace(/([A-Z])/g, " $1").replace(/^./, function (str) {
    return str.toUpperCase();
  });
}

function GenericForm({
  schema,
  data,
  setData,
  name,
  display,
  format = keyToFieldName,
  arrayAction = undefined,
}) {
  // Schema and data have same structure.
  // But schema must be immutable.
  // data is the mutable prop (connected to state)
  // Any array inside schema must be homogenous
  // and must contain at least one object
  // defining the structure of each element.
  // arrayAction is an object of the form:
  // {
  //     content: "JSX containing elements inside button",
  //     call: "A function that element and index of the array element as parameter"
  // }
  // If the param is not undefined,
  // Corresponding button is appended at one level deep to each array element

  let keys = Object.keys(schema);
  let classes = useStyles();
  return (
    <div className={classes.accordionRoot}>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls={name + "-content"}
          id={name + "-header"}
        >
          <Typography className={classes.accordionHeading}>
            {format(name)}
            {tooltips[name] ? (
              <Tooltip
                title={
                  <div
                    style={{
                      fontSize: "1rem",
                      padding: "0.5rem",
                      lineHeight: "1.3rem",
                    }}
                  >
                    {tooltips[name]}
                  </div>
                }
                placement="right"
                style={{
                  display: "inline-block",
                  cursor: "pointer",
                }}
                arrow
              >
                <HelpOutlineIcon
                  style={{
                    fontSize: "1rem",
                    color: "gray",
                    marginLeft: "1rem",
                  }}
                />
              </Tooltip>
            ) : (
              <div></div>
            )}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <div style={{ flexDirection: "column" }}>
            {keys.map((k) => {
              if (display(k)) {
                if (Array.isArray(schema[k])) {
                  // Support only array of objects
                  return (
                    <Accordion>
                      <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls={name + "-" + k + "-content"}
                        id={name + "-" + k + "-header"}
                      >
                        <Typography className={classes.accordionHeading}>
                          {format(k)}
                          {tooltips[k] ? (
                            <Tooltip
                              title={
                                <div
                                  style={{
                                    fontSize: "1rem",
                                    padding: "0.5rem",
                                    lineHeight: "1.3rem",
                                  }}
                                >
                                  {tooltips[k]}
                                </div>
                              }
                              placement="right"
                              style={{
                                display: "inline-block",
                                cursor: "pointer",
                              }}
                              arrow
                            >
                              <HelpOutlineIcon
                                style={{
                                  fontSize: "1rem",
                                  color: "gray",
                                  marginLeft: "1rem",
                                }}
                              />
                            </Tooltip>
                          ) : (
                            <div></div>
                          )}
                        </Typography>
                      </AccordionSummary>
                      <AccordionDetails style={{ flexDirection: "column" }}>
                        <Button
                          variant="contained"
                          color="primary"
                          style={{ width: "10px" }}
                        >
                          <AddIcon
                            className={classes.icon}
                            onClick={() => {
                              data[k].push(cloneDeep(schema[k][0]));
                              setData();
                            }}
                          />
                        </Button>
                        {data[k].map((elt, idx) => (
                          <Card className={classes.cardRoot}>
                            <CardHeader
                              avatar={
                                <Button variant="contained" color="secondary">
                                  <DeleteIcon
                                    className={classes.icon}
                                    onClick={() => {
                                      data[k].splice(idx, 1);
                                      setData();
                                    }}
                                  />
                                </Button>
                              }
                              title={format(k) + " " + String(idx + 1)}
                            />
                            <CardContent>
                              <GenericForm
                                schema={schema[k][0]}
                                data={elt}
                                name="Parameters"
                                format={format}
                                display={display}
                                setData={setData}
                              />
                              {arrayAction === undefined ? (
                                <div style={{ display: "none" }}></div>
                              ) : (
                                <Button
                                  variant="contained"
                                  color="primary"
                                  id={`${name}-${k}-${idx}-btn`}
                                  onClick={() => {
                                    arrayAction.call(elt, idx);
                                  }}
                                >
                                  {arrayAction.content(elt, idx)}
                                </Button>
                              )}
                            </CardContent>
                          </Card>
                        ))}
                      </AccordionDetails>
                    </Accordion>
                  );
                } else if (typeof schema[k] === "object") {
                  return (
                    <GenericForm
                      schema={schema[k]}
                      data={data[k]}
                      format={format}
                      name={k}
                      display={display}
                      setData={setData}
                    />
                  );
                } else if (typeof schema[k] === "number") {
                  return (
                    <TextField
                      label={format(k)}
                      defaultValue={data[k]}
                      type="number"
                      InputLabelProps={{
                        shrink: true,
                      }}
                      onChange={(e) => {
                        data[k] = Number(e.target.value);
                        setData();
                      }}
                    />
                  );
                } else if (typeof schema[k] === "string") {
                  return (
                    <TextField
                      label={format(k)}
                      defaultValue={data[k]}
                      type="text"
                      InputLabelProps={{
                        shrink: true,
                      }}
                      onChange={(e) => {
                        data[k] = String(e.target.value);
                        console.log(data);
                        setData();
                      }}
                    />
                  );
                } else if (typeof schema[k] === "boolean") {
                  return (
                    <FormControlLabel
                      control={
                        <Checkbox
                          checked={data[k]}
                          name={name + "-" + k + "-checkbox"}
                          color="primary"
                        />
                      }
                      label={format(k)}
                      onChange={() => {
                        data[k] = !data[k];
                        setData();
                      }}
                    />
                  );
                } else {
                  return <div style={{ display: "none" }}></div>;
                }
              } else {
                return <div style={{ display: "none" }}></div>;
              }
            })}
          </div>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}

export default GenericForm;
