export const campusReducer = (state, action) => {
  if (action.type === "CAMPUS_SAVE_DATA") {
    return {
      ...state,
      campus: action.data,
      dayRange: [0, action.data.totalSimulatedDays],
    };
  } else if (action.type === "CAMPUS_RESET_DATA") {
    return {
      ...state,
      campus: defaultCampus,
      dayRange: [0, defaultCampus.totalSimulatedDays],
    };
  } else {
    console.log(action);
    console.warn("Action not recognized.");
    return state;
  }
};
