import React from "react";
import { cloneDeep } from "lodash";
import { withRouter } from "react-router-dom";
import CircularProgress from "@material-ui/core/CircularProgress";
import CheckIcon from "@material-ui/icons/Check";
import { connect } from "react-redux";
import { saveScenarios } from "../actions/scenarioActions";
import { defaultScenarioCollection } from "../reducers/scenarioReducer";
import { transaction } from "../connector/socketConnector";
import GenericForm from "./GenericForm";
import Button from "@material-ui/core/Button";

function CustomForm({ campus, scenarios, simulations, dispatch, history }) {
  const triggerSimulate = (dat, idx) => {
    // Please control the animation
    // May require converting transaction to return a Promise.
    transaction(dat, campus, idx);
  };
  let data = cloneDeep(scenarios);
  const setData = () => {
    dispatch(saveScenarios(data));
  };

  console.log(simulations);
  return (
    <div className='form-container'>
      <GenericForm
        schema={defaultScenarioCollection}
        data={data}
        name='Scenarios'
        display={(k) => true}
        setData={setData}
        arrayAction={{
          content: (elt, idx) => (
            <div>
              Simulate
              <CircularProgress
                color='white'
                className='progressbar'
                style={{
                  display:
                    idx in simulations && simulations[idx].complete === false
                      ? "block"
                      : "none",
                }}
              />
              <CheckIcon
                className='completeicon'
                style={{
                  display:
                    idx in simulations && simulations[idx].complete === true
                      ? "block"
                      : "none",
                }}
              />
            </div>
          ),
          call: triggerSimulate,
        }}
      />

      <Button
        variant='contained'
        color='primary'
        onClick={() => {
          history.push("/graphs");
        }}
      >
        Go to Graphs
      </Button>
    </div>
  );
}

const mapStateToProps = (state, props) => {
  return {
    ...state,
    ...props,
  };
};

export default withRouter(connect(mapStateToProps)(CustomForm));
