import { cloneDeep } from "lodash";
import { backend_url } from "../connector/socketConnector";
import csv from "csv";

export const connectionReducer = (state, action) => {
  if (action.type === "IO_STORE_ID") {
    let newState = cloneDeep(state);

    newState.simulations[action.data.caller] = action.data;

    return newState;
  } else if (action.type === "IO_SIM_COMPLETE") {
    let newState = cloneDeep(state);
    newState.simulations[action.data.caller].complete = true;
    fetch(
      `${backend_url}/static/results/${
        newState.simulations[action.data.caller].id
      }/CAMPUS_data.csv`
    )
      .then((data) => data.text())
      .then((data) => {
        csv.parse(
          data,
          {
            columns: true,
          },
          (err, response) => {
            newState.simulationData[action.data.caller] = response;
          }
        );
      });

    return newState;
  } else {
    return state;
  }
};
