import React, { useState } from "react";
import { connect } from "react-redux";
import { makeStyles } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from "@material-ui/icons/Delete";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import { addGraph } from "../actions/graphActions";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "95%",
    margin: "auto",
    padding: "1rem",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

function GraphForm({ dispatch, graphDisplay, simulationData }) {
  const classes = useStyles();
  const defaultData = {
    scenario: "",
    column: "",
  };
  const [value, setValue] = useState([defaultData]);
  const updateData = (e, idx, key) => {
    let newValue = [...value];
    newValue[idx][key] = e.target.value;
    setValue(newValue);
  };

  const showGraph = () => {
    let all_ok = true;
    value.forEach((val) => {
      if (val.column === "" || val.scenario === "") all_ok = false;
    });

    if (all_ok) {
      dispatch(addGraph(value));
      setValue([defaultData]);
    } else {
      alert("Please set all values correctly");
    }
  };
  return (
    <Card>
      <CardHeader
        avatar={
          <Button variant="contained" color="primary" style={{ width: "10px" }}>
            <AddIcon
              className={classes.icon}
              onClick={() => {
                setValue([
                  ...value,
                  {
                    ...defaultData,
                  },
                ]);
              }}
            />
          </Button>
        }
        title="Add Columns to plot"
      />
      <CardContent>
        {value.map((elt, idx) => (
          <div>
            <Button
              variant="contained"
              color="primary"
              style={{ width: "10px", borderRadius: 5, backgroundColor: "red" }}
            >
              <DeleteIcon
                className={classes.icon}
                onClick={() => {
                  setValue(value.filter((element, id) => id !== idx));
                }}
              />
            </Button>
            <FormControl className={classes.formControl}>
              <InputLabel id={"scenario-selector" + idx}>Scenario</InputLabel>
              <Select
                labelId={"scenario-selector" + idx}
                id={"select-scenario-selector" + idx}
                value={elt.scenario}
                onChange={(e) => {
                  updateData(e, idx, "scenario");
                }}
              >
                {Object.keys(simulationData).map((elt) => (
                  <MenuItem value={elt}>{elt}</MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl className={classes.formControl}>
              <InputLabel id={"column-selector" + idx}>Column</InputLabel>
              <Select
                labelId={"column-selector" + idx}
                id={"select-column-selector" + idx}
                value={elt.column}
                onChange={(e) => {
                  updateData(e, idx, "column");
                }}
              >
                {value[idx].scenario in simulationData ? (
                  Object.keys(
                    simulationData[value[idx].scenario][0]
                  ).map((elt) => <MenuItem value={elt}>{elt}</MenuItem>)
                ) : (
                  <div></div>
                )}
              </Select>
            </FormControl>
          </div>
        ))}

        <Button variant="contained" color="primary" onClick={showGraph}>
          Display Graph
        </Button>
      </CardContent>
    </Card>
  );
}

const mapStateToProps = (state, props) => {
  return {
    ...props,
    graphDisplay: state.graphDisplay,
    simulationData: state.simulationData,
  };
};

export default connect(mapStateToProps)(GraphForm);
