export const changeRange = (payload) => {
    return {
        "type": "GRAPH_CHANGE_RANGE",
        data: payload
    }
}

export const addGraph = (payload) => {
    return {
        "type": "GRAPH_ADD_ONE",
        data: payload
    }
}

export const removeGraph = (id) => {
    return {
        "type": "GRAPH_REMOVE_ONE",
        data: id
    }
}