import { defaultCampus, campusReducer } from "./campusReducer";
import { defaultScenarioCollection, scenarioReducer } from "./scenarioReducer";
import { connectionReducer } from "./connectionReducer";
import { graphReducer } from "./graphReducer";

const initState = {
  campus: defaultCampus,
  scenarios: defaultScenarioCollection,
  simulations: {},
  dayRange: [0, defaultCampus.totalSimulatedDays],
  simulationData: {},
  graphDisplay: [],
};

export default function rootReducer(state = initState, action) {
  if (action.type.startsWith("CAMPUS_")) {
    return campusReducer(state, action);
  } else if (action.type.startsWith("SCENARIO_")) {
    return scenarioReducer(state, action);
  } else if (action.type.startsWith("IO_")) {
    return connectionReducer(state, action);
  } else if (action.type.startsWith("GRAPH_")) {
    return graphReducer(state, action);
  } else {
    console.log(action);
    console.warn("Action not recognized");
    return state;
  }
}
