

export const defaultScenario = {
    ComplianceRate:0.8,
    // initiallyInfected: 2,
    
}

export const defaultScenarioCollection = {
    scenarioDetails: [
        defaultScenario
    ]
}

export const scenarioReducer = (state, action) => {
    if (action.type === "SCENARIO_SAVE_DATA"){
        return {
            ...state,
            scenarios: action.data
        }
    }else{
        console.log(action);
        console.warn("Action not recognized");

        return state;
    }
}

