import React from "react";
import EcoTwoToneIcon from "@material-ui/icons/EcoTwoTone";
import "../style.css";

export class CardTab extends React.Component {
  render() {
    let color = this.props.color;
    return (
      <div className="card">
        <p>{this.props.title}</p>
        <div style={{ position: "relative" }}>
          <h2>{this.props.stats}</h2>
          <EcoTwoToneIcon
            style={{
              position: "absolute",
              right: "0",
              top: "0",
              fontSize: "3rem",
              color: "lightblue",
            }}
          />
        </div>
        <p style={{ color: color }}>{this.props.change}%</p>
        <p>Since {this.props.duration}</p>
      </div>
    );
  }
}

