import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import { cloneDeep } from "lodash";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { defaultCampus } from "../reducers/campusReducer";
import { savecampus } from "../actions/campusActions";
import GenericForm from "./GenericForm";
import "../style.css";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "95%",
    margin: "auto",
    padding: "1rem",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
}));

function CustomForm({ campus, history, dispatch }) {
  const classes = useStyles();
  let data = cloneDeep(campus);
  const saveData = () => {
    dispatch(savecampus(data));
    history.push("/scenarios");
  };

  const setData = () => {
    dispatch(savecampus(data));
  };
  return (
    <div className='form-container'>
      <div
        style={{
          width: "90%",
          height: "90%",
          margin: "auto",
          padding: "2rem 0",
        }}
      >
        <GenericForm
          schema={defaultCampus}
          data={data}
          name='campus Parameters'
          display={(k) => true}
          setData={setData}
        />
        <div className={classes.btnRoot}>
          <Button
            variant='contained'
            color='primary'
            onClick={() => {
              saveData();
            }}
          >
            Save and Continue
          </Button>
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state, props) => {
  return {
    ...props,
    campus: state.campus,
  };
};

export default withRouter(connect(mapStateToProps)(CustomForm));
