import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Slider from "@material-ui/core/Slider";
import { connect } from 'react-redux';
import { changeRange } from '../actions/graphActions';

const useStyles = makeStyles({
  root: {
    width: 240,
    color: "white",
    padding: "1.5rem",
    textAlign: "center",
    float: "right",
  },
});

function valuetext(value) {
  return `${value}Â°C`;
}

function RangeSlider({ maxDay, value, dispatch }) {
  const classes = useStyles();

  const handleChange = (event, newValue) => {
    dispatch(changeRange(newValue));
  };

  return (
    <div className={classes.root}>
      <Typography id="range-slider" gutterBottom>
        Date Range
      </Typography>
      <Slider
        value={value}
        onChange={handleChange}
        valueLabelDisplay="auto"
        aria-labelledby="range-slider"
        getAriaValueText={valuetext}
        style={{ color: "#5e72e4" }}
        max={maxDay}
      />
    </div>
  );
}

const mapStateToProps = (state, props) => {
  return {
    maxDay: state.campus.totalSimulatedDays,
    value: state.dayRange,
    ...props
  }
};

export default connect(mapStateToProps)(RangeSlider);