import socket from "socket.io-client";
import { idReceive, simComplete } from "../actions/connectionActions";
import mapFrontendToBackend from "./mapper";

const backend_url = "http://localhost:5000";

var io;
const createConnection = (dispatch) => {
  io = socket(backend_url);

  io.on("sendid", (data) => {
    dispatch(
      idReceive({
        ...data,
        complete: false,
      })
    );
  });

  io.on("senddata", (data) => {
    console.log(data.log);

    if (data.log.indexOf("SIMULATION END") !== -1) dispatch(simComplete(data));
  });

  return io;
};

const transaction = (data, campus, idx) => {
  io.emit("runsim", {
    caller: idx,
    data: JSON.stringify(
      mapFrontendToBackend({
        ...campus,
        ...data,
      })
    ),
  });
};

export { createConnection, transaction, io, backend_url };
