import React from "react";
import { connect } from "react-redux";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import DeleteIcon from "@material-ui/icons/Delete";
import { makeStyles } from "@material-ui/core";
import { removeGraph } from "../actions/graphActions";
import {
  LineChart,
  XAxis,
  YAxis,
  Legend,
  Tooltip,
  CartesianGrid,
  Line,
} from "recharts";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "95%",
    margin: "auto",
    padding: "1rem",
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

function Graph({ dispatch, maxDay, simulationData, graphDisplay, dayRange }) {
  const classes = useStyles();
  const aggregate = (cols) => {
    let data = [];
    for (let i = dayRange[0]; i < dayRange[1]; i++) {
      let daily = {};
      cols.forEach((col) => {
        daily[`Scenario-${col.scenario} ${col.column}`] = Number(
          simulationData[col.scenario][i][col.column]
        );
      });
      daily["day"] = Number(simulationData[cols[0].scenario][i]["DAY"]);
      data.push(daily);
    }

    return data;
  };
  return (
    <div>
      {graphDisplay.map((elt, idx) => (
        <Card>
          <CardHeader
            avatar={
              <Button
                variant='contained'
                color='secondary'
                style={{ width: "10px" }}
              >
                <DeleteIcon
                  className={classes.icon}
                  onClick={() => {
                    dispatch(removeGraph(idx));
                  }}
                />
              </Button>
            }
            title={`Graph ${idx + 1}`}
          />
          <CardContent>
            {(() => {
              const data = aggregate(elt);
              return (
                <LineChart
                  data={data}
                  width={600}
                  height={400}
                  margin={{
                    top: 10,
                    right: 10,
                    left: 10,
                    bottom: 10,
                  }}
                >
                  <CartesianGrid strokeDasharray='3.3' />
                  <Tooltip />
                  <XAxis
                    dataKey='day'
                    label='Day'
                    type='number'
                    domain={dayRange}
                  />
                  <YAxis />
                  <Legend />
                  {Object.keys(data[0]).map((datakey) =>
                    datakey !== "day" ? (
                      <Line
                        type='monotone'
                        dataKey={datakey}
                        stroke='red'
                        dot={{ r: 1 }}
                      />
                    ) : (
                      <div></div>
                    )
                  )}
                </LineChart>
              );
            })()}
          </CardContent>
        </Card>
      ))}
    </div>
  );
}

const mapStateToProps = (state, props) => {
  return {
    ...state,
    ...props,
    maxDay: state.campus.totalSimulatedDays,
  };
};

export default connect(mapStateToProps)(Graph);
