// Maps frontend fields to Backend fields.
export default function mapFrontendToBackend(data) {
  let ans = {};
  ans["Virus_Deathrates"] = [];
  ans["Virus_IncubationPeriod"] = [];
  ans["Virus_ProbSeverity"] = [];
  ans["Comorbidty_matrix"] = {
    ComorbidX: [],
  };

  data.populationDistribution.forEach((population) => {
    ans["Virus_Deathrates"].push(population.deathRate);
    ans["Virus_IncubationPeriod"].push(population.incubationPeriod);
    ans["Virus_ProbSeverity"].push([
      population.probabilityOfSeverity.mild,
      population.probabilityOfSeverity.mediocre,
      population.probabilityOfSeverity.severe,
    ]);
    ans["Comorbidty_matrix"]["ComorbidX"].push(
      population.probabilityOfComorbidity
    );
  });

  //###########################Virus######################
  let fcr = data.virusParameters.fullCapacampusRatio;
  ans["Virus_FullCapRatio"] = [
    fcr.hospital,
    fcr.covidHealthCare,
    fcr.covidIsolationCentre,
  ];
  ans["Virus_InitialTestingCap"] =
    data.virusParameters.initialTestingCapacampus;
  ans["Virus_ExpectedCureDays"] = data.virusParameters.expectedDaysToCure;
  ans["Virus_R0"] = data.virusParameters.r0;
  ans["Virus_Prob_ContactTracing"] =
    data.virusParameters.probabilityOfContactTracing;
  ans["Virus_Params"] = {
    Transport: data.virusParameters.transportExposureParameter,
    Home: data.virusParameters.homeExposureParameter,
    Grocery: data.virusParameters.groceryExposureParameter,
    Unemployed: data.virusParameters.unemployedExposureParameter,
    Random: data.virusParameters.randomExposureParameter,
  };

  ans["SIMULATION_DAYS"] = data.totalSimulatedDays;

  ans["Initial_Compliance_Rate"] = data.ComplianceRate;
  // ans["INIT_INFECTED"] = data.initiallyInfected;

  return ans;
}
